
import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { Validators, FormControl, FormGroup, FormBuilder, AbstractControl } from '@angular/forms';
import { SelectItem, Message, ConfirmDialogModule, ConfirmationService } from 'primeng/primeng';
import { SelectCustGroupBean } from './bean/selectCustGroup.bean';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { Router, ActivatedRoute, Params } from '@angular/router';
import * as _ from 'lodash';
import { specialSymbol, checkNumber, checkEnAndNum } from 'app/@uisftech/common/service/validator';
import { tableMessage } from 'app/@uisftech/common/constant/config';
import { CommonHttpService } from "app/pages/tzb/common/http/common.http.service";
import { CusOperationHttpService } from 'app/pages/tzb/custom/http/custom-operation/custom-operation.http.service';

@Component({
  selector: 'app-customCenter-group-query',
  templateUrl: './custom-group-query.component.html',
  styleUrls: ['./custom-group-query.component.scss'],
  providers: [ConfirmationService, Commfunc, CommonHttpService]
})
export class CustomCenterGroupQueryComponent implements OnInit {
  orgId2: any;
  @Input() quickquery:any;
  @Input() guestMsg:any;
  @Output() outValue: EventEmitter<any> = new EventEmitter();
  title: string = '客户群列表';
  selectedCity: string;
  selectedCities: any[];
  // 表单校验
  form1: FormGroup;
  form2: FormGroup;

  // bean
  selectCustGroupBean: SelectCustGroupBean = new SelectCustGroupBean();//客户群查询Bean
  //公共
  tableMesg: any = tableMessage;
  // 码值
  groupType: any = [];//客户群类型
  groupStatus: any = []//群状态下拉框
  // 控件
  msgs: Message[] = [];
  first: any = 0;

  disabledMain: boolean = false;

  isN: boolean = true;
  isAll: boolean = true;
  // 传参
  //获取所有码值
  code: any;
  // 中间量
  groupname: any;//查询范围
  groupTypeSel: any = '';//客户群类型
  groupStatusSel: any = '1';//群状态
  groupId: string;//群号
  groupName: string;//群名称
  address: any = '';//群地址
  // 机构
  orgFlag: boolean = false;//所属机构flag  
  orgIdTemp: any = ''//所属机构取报文头  
  orgId: any = '';//所属机构
  orgName: any = '';//机构名
  orgNameArr: any = [];//机构名
  orgNameTemp: any = '';
  // 群主
  ownerFlag: boolean = true;//群主flag
  ownerId: any = '';//群主
  ownerName: string = '';
  ownerNameArr: any = [];//群主
  ownerIdTemp: any = '';//群主


  // 展示中间量
  contactList: any = '';
  introducerList: any = '';
  mentorList: any = '';
  //
  pageNum: any = 1;
  pageSize: any = 10;
  totalNum: any;
  // 机构
  orgDisplay: boolean = false;
  //
  mesg: any;//session

  constructor(
    public fb: FormBuilder,
    public httpService: CusOperationHttpService,
    public commfunc: Commfunc,
    public router: Router,
    public routerInfo: ActivatedRoute,
    public confirmationService: ConfirmationService,
    public commonHttpService: CommonHttpService,

  ) {
    this.codeValues();
    this.groupType = this.code['GroupTypeSave'];
    this.groupStatus = this.code['GroupStatus']
  }

  ngOnInit() {
    // 初始化
    this.quickQueryLoad();
    this.preSet();
    this.owerListQuery();
    // 默认查询一次
    this.doCheck();

    this.clickSearchScope();
  }
  //码值
  codeValues() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code = ret //code是自己声明的变量
  }
  //快捷查询初始化
  quickQueryLoad(){
    if(this.guestMsg!=undefined){
      if(this.guestMsg.dataType=='00'){
        this.groupName=this.guestMsg.dataMsg;
        this.groupId=undefined;
      }
      if(this.guestMsg.dataType=='01'){
        this.groupId=this.guestMsg.dataMsg;
        this.groupName=undefined;
      }
    }
  }
  preSet() {
    // this.form1 = this.fb.group({
    // groupId1: new FormControl('', [checkEnAndNum]),
    // 'lastname': new FormControl('', Validators.required),
    // 'password': new FormControl('', Validators.compose([Validators.minLength(6)])),
    // 'description': new FormControl(''),
    // 'gender': new FormControl('', Validators.required)
    // });
    // this.form2 = this.fb.group({
    //   'groupName': new FormControl('', [specialSymbol])
    // });
    // 初始化
    // this.selectCustGroupBean.pageSize = 5;
    // this.selectCustGroupBean.pageNum = 1;
    this.groupname = '2';//查询范围
    // this.selectCustGroupBean.searchScope = '1';
    // 群主 session
    let session = window.sessionStorage;
    let data = session.getItem('commonHeader');
    if (!data) {
      this.router.navigate(["/login"]);
      return;
    }
    this.mesg = JSON.parse(this.commfunc.getSessionData('commonHeader'));
    let mesg = JSON.parse(this.commfunc.getSessionData('commonHeader'));
    let ch = JSON.parse(this.commfunc.getSessionDataCH('chName'));
    this.ownerIdTemp = mesg['userId'];//群主id
    this.orgIdTemp = mesg.extParam['orgId'];//机构id
    this.selectCustGroupBean.orgId = _.clone(this.orgId);
    this.ownerName = ch['userName'];//群主名
    this.orgNameTemp = ch['orgName'];//机构名
    console.log(mesg.extParam.managePermission)
    if (mesg.extParam.managePermission && mesg.extParam.managePermission == 'N') {
      this.isN = false;
      this.isAll = false;
      this.ownerId = _.clone(mesg['userId']);
      this.groupname = '3';
      this.selectCustGroupBean.orgId = null;

    } else {
      this.ownerFlag = false;
    }
    if (!mesg) {
    }

    // this.orgName = _.clone(this.orgNameTemp);
    // 机构列表
    let tempArr = [{ label: '请选择', value: '' }];
    tempArr.push({ label: this.orgName, value: this.orgIdTemp });
    this.orgNameArr = _.clone(tempArr);
    // 群主列表
    this.ownerNameArr = [{ label: '请选择', value: '' }];
    this.ownerNameArr.push({
      label: this.ownerName,
      value: this.ownerIdTemp,
    })

    //我的客户群辅管群传参
    if (this.routerInfo.snapshot.params['searchScope']) {
      this.groupname = this.routerInfo.snapshot.params['searchScope'];//查询范围--辅管群
    }
    this.orgId = '';
  }
  clickSearchScope() {
    //点击范围触发flag
    if (this.groupname != '1') {
      this.isAll = true;
      this.orgId = _.clone(this.orgIdTemp);
      // this.orgName = _.clone(this.orgNameTemp);
      this.ownerId = _.clone(this.ownerIdTemp);
      this.orgFlag = true;
    }
    else {
      this.orgId = '';
      this.orgName = '';
      this.ownerId = '';
      this.orgFlag = false;
      this.isAll = false;
    }
    // if (this.groupname != '2' && this.groupname != '1') {
    //   this.orgId = _.clone(this.orgIdTemp);
    //   // this.orgName = _.clone(this.orgNameTemp);
    //   this.ownerId = _.clone(this.ownerIdTemp);
    //   this.ownerFlag = true;
    // }
    // else 
    if (this.groupname == '3') {
      this.ownerFlag = true;
      this.isAll = false;
      this.ownerChoose();
      this.disabledMain = true;
    }
    else if (this.groupname == '4') {
      this.ownerFlag = false;
      this.isAll = false;
      this.ownerId2 = '';
      this.ownerName2 = '';
      this.disabledMain = false;
      // if(!this.isN){
      //   this.ownerId2 = this.mesg.userId;
      // }
      // this.doCheck();
    }
  }
  enterSelect() {
    // 输入条件
    this.selectCustGroupBean.searchScope = _.clone(this.groupname);//查询范围
    this.selectCustGroupBean.groupType = _.clone(this.groupTypeSel);//客户群类型
    this.selectCustGroupBean.groupStatus = _.clone(this.groupStatusSel);//群状态
    this.selectCustGroupBean.groupId = _.clone(this.groupId);//群号
    this.selectCustGroupBean.groupName = _.clone(this.groupName);//群名称
    this.selectCustGroupBean.address = _.clone(this.address);//群地址
    // this.selectCustGroupBean.orgId = _.clone(this.orgIdTemp);//所属机构
    this.selectCustGroupBean.ownerId = _.clone(this.ownerId2);//所属机构    
  }
  orgChange(event) {

  }
  ownerChange(event) {

  }
  doCheck() {
    //校验的
    // for (const i in this.form1.controls) {
    //   this.form1.controls[i].markAsDirty();
    // }
    // for (const i in this.form2.controls) {
    //   this.form2.controls[i].markAsDirty();
    // }
    // //处理是否提交
    // for (const i in this.form1.controls) {
    //   if (!this.form1.controls[i].valid) {
    //     return;
    //   }
    // }
    // for (const i in this.form2.controls) {
    //   if (!this.form2.controls[i].valid) {
    //     return;
    //   }
    // }
    // 2017.12.19
    this.selectCustGroupBean.pageSize = 10;
    this.selectCustGroupBean.pageNum = 1;
    this.first = 0;
    this.selectCustGroup();
  }
  selectCustGroup() {
    // 查询客户群
    this.enterSelect();//输入条件
    this.isNull();//非空校验
    // this.selectCustGroupBean.pageNum = this.pageNum;
    // this.selectCustGroupBean.pageSize = this.pageSize;
    //权限判断
    if (this.mesg.extParam.managePermission && this.mesg.extParam.managePermission == 'N') {
      this.selectCustGroupBean.orgId = undefined;
      if (this.groupname == 3) {
        this.selectCustGroupBean.ownerId = this.mesg.userId;
      }
      this.isAll = false;
    }
    //  else {
    //   this.selectCustGroupBean.orgId = undefined;
    // }
    // console.log(this.selectCustGroupBean);
    this.selectCustGroupBean.orgId = this.orgId2;
    this.httpService.selectCustGroup(this.selectCustGroupBean).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.totalNum = data.totalNum;
        this.selectCustGroupBean = data;
        this.selectCustGroupBean.groupList = data.groupList;
        this.adminSelect();// 管理人员查询赋值
        this.selectCustGroupBean.groupList.forEach((element1, i) => {
          let arr = [];
          if (element1.contactList.length != 0) {
            element1.contactList.forEach(element2 => {
              arr.push(element2.roleName)
            });
            this.selectCustGroupBean.groupList[i].contactList = arr;
            console.log(this.selectCustGroupBean.groupList[i].contactList)
          };
          // console.log(element1)
          if (element1.createdStamp != null && element1.createdStamp != '') {
            element1.createdStamp = element1.createdStamp.split(' ')[0]
          }
        });
        this.msgs = [];
        this.msgs = [{ severity: 'success', summary: '提示', detail: '查询成功！' }];
      }
      else {
        this.msgs = [];
        data.returnCode.message ? data.returnCode.message : '查询失败！';
        this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
      }
    }, err => {
      this.msgs = [];
      this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败！' }];
    })
  }
  adminSelect() {
    // 管理人员查询赋值

    for (let i in this.selectCustGroupBean.groupList) {
      this.selectCustGroupBean.groupList[i].contactListArr = [];
      for (let j in this.selectCustGroupBean.groupList[i].contactList) {
        let temp = _.clone(this.selectCustGroupBean.groupList[i].contactList[j].roleName)
        this.selectCustGroupBean.groupList[i].contactListArr.push(temp);
      }
    }
    for (let i in this.selectCustGroupBean.groupList) {
      this.selectCustGroupBean.groupList[i].introducerListArr = [];
      for (let j in this.selectCustGroupBean.groupList[i].introducerList) {
        let temp = _.clone(this.selectCustGroupBean.groupList[i].introducerList[j].roleName)
        this.selectCustGroupBean.groupList[i].introducerListArr.push(temp);
      }
    }
    for (let i in this.selectCustGroupBean.groupList) {
      this.selectCustGroupBean.groupList[i].mentorListArr = [];
      for (let j in this.selectCustGroupBean.groupList[i].mentorList) {
        let temp = _.clone(this.selectCustGroupBean.groupList[i].mentorList[j].roleName)
        this.selectCustGroupBean.groupList[i].mentorListArr.push(temp);
      }
    }
  }


  paginate(event) {
    this.selectCustGroupBean.pageSize = event.rows;
    let currentPage = event.page + 1;
    this.selectCustGroupBean.pageNum = currentPage;
    this.first = (currentPage - 1) * this.selectCustGroupBean.pageSize;
    this.selectCustGroup();
  }
  reset() {
    this.groupId = '';
    this.groupTypeSel = '';
    this.groupStatusSel = '1';
    this.address = '';
    this.orgId = '';
    this.ownerId = '';
    this.orgName = '';
    if (this.isN || this.groupname == '4') {
      this.ownerName2 = '';
    }
    this.ownerId2 = '';
    this.groupName = '';
    // this.selectCustGroupBean.pageSize = 5; 
    // this.selectCustGroupBean.pageNum = 1;
    this.first = 0;
    this.selectCustGroupBean = new SelectCustGroupBean();
    this.doCheck();
  }

  // 其他
  isNull() {
    // 非空校验
    if (this.selectCustGroupBean.searchScope.search(/^\S+$/) || !this.selectCustGroupBean.searchScope) {
      this.msgs = [];
      this.msgs = [{ severity: 'error', summary: '提示', detail: '查询范围不能为空！' }];
      return;
    }
    if (this.selectCustGroupBean.searchScope == '2') {
      // this.selectCustGroupBean.orgId = _.clone(this.orgIdTemp);
    }
  }
  tocreategroup() {
    this.router.navigate(['/pages/tzb/custom/custom-group/create-custom-group'])
  }
  toGroupView(col) {
    console.log(col);
    // 群编号根据不同状态跳转
    if (col.operatePermission == 'N') {
      this.msgs = [];
      this.msgs = [{ severity: 'error', summary: '提示', detail: '你不能操作该群！' }];
    } else {
      if (col.groupStatus === '1') {
        let a = 'groupId=' + col.groupId;
        let b = encodeURI(a);
        window.open('/#/pages/tzb/custom/custom-group/custom-group-view;' + b);
        // this.router.navigate(['/pages/tzb/custom/custom-group/custom-group-view', { groupId: col.groupId, groupName: col.groupName }])
      } else if (col.groupStatus === '2') {
        this.router.navigate(['/pages/tzb/custom/custom-group/custom-group-save', { groupId: col.groupId }])
      } else if (col.groupStatus === '4') {
        this.router.navigate(['/pages/tzb/custom/custom-group/custom-group-save', { groupId: col.groupId }])
      } else {
        this.msgs = [];
        this.msgs = [{ severity: 'error', summary: '提示', detail: '你不能操作该群！' }];
      }
    }
  }
  selectOrg() {
    // 选择机构
    this.orgDisplay = true;
    document.body.style.overflow = "hidden";
  }
  onHidden() {
    // 退出机构模态框
    document.body.style.overflow = 'auto';
  }
  returnOrg(event) {
    // console.log(event)
    if (event.hasOwnProperty('orgName')) {
      this.orgName = event.orgName;
      this.orgIdTemp = event.orgId;
      this.selectCustGroupBean.orgId = event.orgId;
      this.orgId2= event.orgId;
    }
    this.orgDisplay = false;
    document.body.style.overflow = 'auto';
  }

  owerListQuery() {
    let location = JSON.parse(this.commfunc.getSessionData('commonHeader'));
    console.log(location)
    let param = {

    }
    this.commonHttpService.queryOrgPostTellerRelation(param).subscribe(data => {
      if (data.returnCode.code == "success") {
        if (data.orgPostTellerList && data.orgPostTellerList.length > 0) {
          this.ownerNameArr = [{
            label: '请选择',
            value: '',
          }];
          data.orgPostTellerList.forEach(item => {
            this.ownerNameArr.some(item_2 => {
              return item.userLoginId == item_2.value;
            }) ? '' :
              this.ownerNameArr.push({
                label: item.userName,
                value: item.userLoginId,
              });
          });
          console.log(this.ownerNameArr)
        }
        console.log(data)
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
    })
  }
  //按钮权限
  permissionCheck(id) {
    return this.commfunc.permissionCheck(id)
  }


  //群主选择模态框
  ownerDisplay: boolean = false;
  ownerId2: string;
  ownerName2: string;
  ownerChoose() {
    if (!this.isN && this.groupname == 3) {
      let ch = JSON.parse(this.commfunc.getSessionDataCH('chName'));

      this.ownerId2 = this.mesg.userId;
      this.ownerName2 = ch.userName;
      // console.log(ch);
      // console.log(this.mesg);
    } else {
      this.ownerDisplay = true;
    }
  }
  returnOwnerInfo(event) {
    // console.log(event)
    this.ownerDisplay = false;

    this.ownerId2 = event[0].tellerId;
    this.ownerName2 = event[0].tellerName;
    console.log(this.ownerId2);
  }

}
