import { Component, OnInit, Output, Input, OnChanges, EventEmitter } from '@angular/core';
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { Message } from 'primeng/primeng';
import { queryCustMangerLoanQualificationByOrg } from './bean/person-choose.bean';
import { CustomCenterHttpService } from 'app/pages/tzb/customCenter/http/custom-center.http.service';

@Component({
    selector: "person-choose",
    templateUrl: './person-choose.component.html',
    styleUrls: ['./person-choose.component.scss'],
})
export class PersonChoose implements OnInit {
    @Output() outValue: EventEmitter<any> = new EventEmitter();
    @Input() personId: any;
    bean: queryCustMangerLoanQualificationByOrg = new queryCustMangerLoanQualificationByOrg();
    pageSize: any;
    total: number = 0;
    first: number = 0;
    tableData: any[];
    tabMesg: any = '暂无数据';
    constructor(
        public http: CustomCenterHttpService,
    ) {

    }
    ngOnInit() {
        this.bean.pageNum=1;
        this.bean.pageSize=10;
        this.queryScale();
    }

    queryScale(){
        this.bean.pageNum=1;
        this.bean.pageSize=10;
        this.first = 0;
        this.quertList();
    }

    quertList() {
        this.bean.partyId=this.personId;
        this.http.queryPermissionByPartyId(this.bean).subscribe(data => {
            if (data['returnCode']['code'] == 'success') {
                this.tableData = data['personList'];
                this.total=data['total'];
            }
        })
    }

    // 分页
    paginate(event) {
        this.bean.pageSize = event.rows * 1;
        let currentPage = event.page + 1;
        this.bean.pageNum = currentPage;
        this.first = (currentPage - 1) * this.bean.pageSize;
        this.quertList();
    }

    // 选中一行
    onRowSelect(event) {
        let personArr = [];
        personArr.push(event.data)
        this.outValue.emit(personArr);
    }

    // 重置
    reset(){
        this.bean.pageNum=1;
        this.bean.pageSize=10;
        this.first = 0;
        this.bean.tellerName = undefined;
        this.bean.tellerId = undefined;
    }
}