import { Component, OnInit, Output, Input, OnChanges, EventEmitter } from '@angular/core';
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { Message } from 'primeng/primeng';
import { ProjectElementGroupListQueryBean, projectElementGroupDetailQueryBean } from './bean/element-combination-list.bean';
import { ProjectHttpService } from '../../http/project-main-manage.service';
import * as _ from 'lodash';



@Component({
    selector: "element-combination-list",
    templateUrl: './element-combination-list.component.html',
    styleUrls: ['./element-combination-list.component.scss'],
})

export class ElementCombinationList implements OnInit {
    @Output() close = new EventEmitter();
    @Input('typeOn') typeOn: any;
    @Input('functiontype') functiontype: any;
    msgs: any;
    code: any;
    options1: any[] = [
        {
            label: "1",
            value: '01',
        }
    ];
    elementGroupFlag: any[];
    multipleFlag: any[];
    mustInput: any[];
    elementType: any[];
    upDataList: any[] = [];
    checkTip: string;
    projectElementGroupListQueryBean: ProjectElementGroupListQueryBean = new ProjectElementGroupListQueryBean();
    projectElementGroupDetailQueryBean: projectElementGroupDetailQueryBean = new projectElementGroupDetailQueryBean();
    tableData: any[] = [
        {
            tit: '123'
        }
    ];

    type: any;
    liet: any;
    listData: any[] = [];
    tableHead: any[] = [
        {
            label: '序号',
            value: 'seqNum'
        },
        {
            label: '要素名称',
            value: 'enementCnName'
        },
        {
            label: '是否为组件',
            value: 'elementGroupFlag'
        },
        {
            label: '允许多条',
            value: 'multipleFlag'
        },
        {
            label: '要素标识',
            value: 'elementEnName'
        },
        // {
        //     label: '是否必输',
        //     value: 'mustInput'
        // },
        // {
        //     label: '要素字段类型',
        //     value: 'elementType'
        // },
        // {
        //     label: '要素精度',
        //     value: 'elementPrecision'
        // },
        // {
        //     label: '要素检查服务名',
        //     value: 'frontElementCheckServe'
        // },
        // {
        //     label: '最小值',
        //     value: 'minValue'
        // },
        // {
        //     label: '最大值',
        //     value: 'maxValue'
        // },
        // {
        //     label: '缺省值',
        //     value: 'defValue'
        // },
        {
            label: '列表值选项',
            value: 'listElement'
        },
    ];
    constructor(
        public httpService: ProjectHttpService,
        public commfunc: Commfunc,
    ) {

    }
    ngOnInit() {
        this.query();
        this.codeValues();
        this.elementType = this.code['elementType'];
        this.elementGroupFlag = this.code['elementGroupFlag'];
        this.multipleFlag = this.code['multipleFlag'];
        this.mustInput = this.code['mustInput'];
        if (this.typeOn == '02') {
            setTimeout(()=>{
                this.post(this.functiontype)
            })
            
        }

    }
    query() {
        this.msgs = [];
        this.httpService.projectElementGroupListQuery(this.projectElementGroupListQueryBean).subscribe(data => {
            if (data.returnCode.code == "success") {
                this.listData = data['returnList'];
                // this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
            } else {
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
        })
    }
    //获取码值
    //码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量
    }
    post(i) {
        this.msgs = [];
        this.projectElementGroupDetailQueryBean.elementGroupId = i
        this.httpService.projectElementGroupDetailQuery(this.projectElementGroupDetailQueryBean).subscribe(data => {
            if (data.returnCode.code == "success") {
                let listDataT = _.clone(data['elementsList'])
                listDataT.forEach(e => {
                    let list = '';
                    e.listElement.forEach(et => {
                        list += et.optionKey + '_' + et.optionValue;
                    })
                    e.listElement = list;
                })
                this.upDataList = listDataT;
                // this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
            } else {
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
        })
        this.close.emit(i)
    }
}